.error {
    display: flex;
    background: url('../../../images/Rectangle.png'), lightgray 0% 0% / 100px 100px repeat;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
    margin: 50px 15px;
    border-radius: 16px;
    height: 90vh;
}
.error > img {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    bottom: 0;
    object-fit: cover;
}
.error svg {
    width: 128px;
height: 128px;
flex-shrink: 0;
}
.error .title {
    display: flex;
    align-items: center;
    color: #000;
    font-family: Inter;
    font-size: 200px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 12px;
}
.error .subtitle {
    color: #000;
    font-family: Inter;
    font-size: 64px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}
.error .paragraph {
    color: #000;
text-align: center;
font-family: Inter;
font-size: 24px;
font-style: normal;
font-weight: 500;
line-height: normal;
}

@media (max-width:650px) {
    .error svg {
        width: 128px;
    height: 128px;
    flex-shrink: 0;
    }
    .error .title {
        font-size: 150px;
    
    }
    .error .subtitle {
        color: #000;
        font-family: Inter;
        font-size: 55px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
    }
    .error .paragraph {
        color: #000;
    text-align: center;
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    }
}

@media (max-width:570px) {
    .error svg {
        width: 110px;
    height: 110px;
    flex-shrink: 0;
    }
    .error .title {
        font-size: 130px;
    }
    .error .subtitle {
        color: #000;
        font-family: Inter;
        font-size: 45px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
    }
    .error .paragraph {
        color: #000;
    text-align: center;
    font-family: Inter;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    }
}
@media (max-width:400px) {
    .error svg {
        width: 90px;
    height: 90px;
    flex-shrink: 0;
    }
    .error .title {
        font-size: 100px;
    }
    .error .subtitle {
        color: #000;
        font-family: Inter;
        font-size: 35px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
    }
    .error .paragraph {
        color: #000;
    text-align: center;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    }
    .error > img {
        object-position: left;
    }
}