.host-part{
    display: flex;
    width: 99%;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 32px;
    position: relative;
}
@media (max-width:400px) {
    .host-part{
        padding: 0;
        padding: 0px 8px;
    }
    
}

.host-title{
    display: flex;
    height: 29px;
    flex-direction: column;
    align-items: flex-start;
    gap: 64px;
    align-self: stretch;
}
.host-title h3{
    color: var(--Black, #100F0F);
    font-family: Inter;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}
@media (max-width:370px) {
    .host-part{
        padding: 0;
        padding: 0px 0px;
    }
    .host-title h3{
        margin-left: 8px;
     }
}
.host-countries-list{
    display: flex;
    flex-wrap: wrap;
    width:100%;
    align-items: center;
    gap: 15px;
}
